import React from "react";
import {
  Page,
  Card,
  Row,
  Col,
  Fab,
  Icon,
  Navbar,
  Link,
  Button,
  CardContent,
  CardFooter,
  CardHeader,
  FabButtons,
  Chip,
  Block,
  Subnavbar,
} from "framework7-react";

import { dict } from "../../Dict";


const RoomIndex = (props) => {
  function videos(spec, id) {
    var width = '140'
    var height = '120'
    if (spec === 'high') {
      width = '240'
      height = ''
    }
    return (
      <div class="col-100 tablet-33" key={"container-" + id}>
        <video
          controls={true}
          key={id}
          id={"video-" + id}
          src=""
          width={width}
          height={height}
          autoPlay
          playsInline
          muted={props.muted}
        />
      </div>
    )
  }

  function highVidoes() {
    var result = [];
    if (props.feeds) {
      {
        props.feeds.map((feed) => {
          //var participant = 

          if (props.feedToRole(feed.id) === 'speaker') {
            console.log(feed.id, 'is a speaker')
            result.push(videos('high', 'h-' + feed.id));
            //props.streamAttacher(feed)
          }

          // if (participant && props.participantIsSpeaker(participant.uuid)) {
          //  
          //  
          // }

        });
      }
    }
    return result;
  }

  function lowVideos() {
    var result = [];
    if (props.feeds) {
      {
        props.feeds.map((feed) => {

          if (props.feedToRole(feed.id) === 'listener') {
            console.log(feed.id, 'is a listener')
            result.push(videos('low', feed.id));
            //props.streamAttacher(feed)
          }
          // var participant = () =>props.findParticpantById(feed.rfid)
          //console.log(props.findParticpantById(feed.rfid))
          // if (participant && !props.participantIsSpeaker(participant.uuid)) {
          //  result.push(videos('low', feed.id));
          //  props.streamAttacher(feed)
          // }

        });
      }
    }
    return result;
  }

  function requests() {
    var result = [];
    if (props.requests) {
      props.requests.map((request) => {
        result.push(
          <div className="chip color-lightblue br-0">
            <a onClick={() => props.removeRequest(request.uuid)}>
              <div className="chip-media br-0 color-lightgray ">
                <i class="fa fa-times" aria-hidden="true"></i>
              </div>
            </a>
            <a onClick={() => props.acceptRequest(request.uuid)}>
              <div className="chip-media br-0 color-teal mr-0">
                <i class="fa fa-level-up" aria-hidden="true"></i>
              </div>
            </a>
            <div className="chip-media br-0 color-lightred mr-0">
              <i class="fa fa-level-down" aria-hidden="true"></i>
            </div>
            <div className="chip-media br-0 color-lightyellow mr-0">
              <i class="fa fa-microphone-slash" aria-hidden="true"></i>
            </div>

            <div className="chip-media br-0 color-darkgray mr-0">
              <i class="fa fa-ban" aria-hidden="true"></i>
            </div>

            <div className="chip-media br-0 color-lightorange mr-0">
              <i class="fa fa-gavel" aria-hidden="true"></i>
            </div>

            <div className="chip-label  mr-2">{request.display}</div>

          </div>

        )
      });
    }
    return result;
  }

  function participants() {
    var result = [];
    if (props.participants) {
      props.participants.map((participant) => {
        result.push(
          <div className="chip br-0">

            <div className="chip-label  mr-2">{participant.display}</div>
          </div>

        )
      });
    }
    return result;
  }

  function title() {
    if (props.room) {
      return (props.room.title)
    }
  }

  return (
    <Page onPageBeforeRemove={() => props.beforeRemove}>
      <Navbar title={title()} backLink={dict.back}>
        <Link panelOpen="right">
          <Icon f7="bars"></Icon>
        </Link>
      </Navbar>
      <Subnavbar className="bg-black subnavbar-black">
        <div></div>
        <div className=''>
          <a onClick={() => props.publishCamera()}><i class="fa fa-video-camera ml-4" aria-hidden="true"></i></a>
          <a onClick={() => props.publishMicrophone()}><i class="fa fa-microphone ml-4" aria-hidden="true"></i></a>
          <a onClick={() => props.sendData()}><i class="fa fa-comment ml-4" aria-hidden="true"></i></a>
          <a onClick={() => props.speakRequest()}><i class="fa fa-hand-paper-o ml-4" aria-hidden="true"></i></a>
        </div>

      </Subnavbar>
      <div style={{ marginTop: '150px;', paddingTop: '150px;' }}>|</div>
      <Block >
        <Row className="center" id="hosts">
          {highVidoes()}
        </Row>
      </Block>

      <Block strong>
        {requests()}
        < hr></hr>
        {participants()}
      </Block >

      <Block >
        {lowVideos()}
      </Block>

    </Page>
  );
};
export default RoomIndex;
